<template>
  <div>
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>查询统计
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 实施统计
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content screenSt"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="62"
      >
        <Row>
          <Col
            :xs="{ span: 5}"
            :lg="{ span: 21}"
          >
          <FormItem label="项目名称">
            <Input
              v-model="searchData.projectName"
              clearable
              placeholder="请填写项目名称"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <Row style="margin-top:15px;">
          <Col
            :xs="{ span: 5}"
            :lg="{ span: 10}"
          >
          <FormItem label="主管单位">
            <Select
              v-model="searchData.manageOrgId"
              clearable
              filterable
            >
              <Option
                v-for="item in orgList"
                :key="item.orgId"
                :value="item.orgId"
              >{{ item.orgName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            :xs="{ span: 11, offset: 1 }"
            :lg="{ span:10}"
          >
          <FormItem label="承担单位">
            <Select
              v-model="searchData.chendanOrgId"
              clearable
              filterable
            >
              <Option
                v-for="item in orgList"
                :key="item.orgId"
                :value="item.orgId"
              >{{ item.orgName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            style="text-align: right"
            :xs="{ span: 1, offset: 1 }"
            :lg="{ span: 1, offset: 1 }"
          >
          </Col>
        </Row>
        <Row style="margin-top:15px;">
          <Col
            :xs="{ span: 5}"
            :lg="{ span: 10}"
          >
          <FormItem label="部门">
            <Select
              v-model="searchData.officeId"
              clearable
              filterable
            >
              <Option
                v-for="item in officeList"
                :key="item.officeId"
                :value="item.officeId"
              >{{ item.officeName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            :xs="{ span: 11, offset: 1 }"
            :lg="{ span:10}"
          >
          <FormItem label="资金类别">
            <Select
              v-model="searchData.capitalId"
              clearable
              filterable
            >
              <Option
                v-for="item in capitalList"
                :key="item.capitalId"
                :value="item.capitalId"
              >{{ item.capitalName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            style="float:right"
            :xs="{ span: 1, offset: 1 }"
            :lg="{ span: 1, offset: 1 }"
          >
          <Button
            clearable
            type="primary"
            @click="getInvestmentData()"
          >
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button icon="md-print" @click.stop="printContent(12)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/statistics/exportImplementationPlanExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="investmentList"
      ></Table>
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!--实施统计弹出框-->
      <implementPlan ref="implementPlan" :search-data="popUpSearchData" />
    </Content>
  </div>
</template>

<script>
import implementPlan from '@/components/implementPlan.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'ImplementPlan',
  components: {
    implementPlan
  },
  mixins: [excelExcel],
  data() {
    return {
      popUpSearchData: {
        auditBatchType: null,
        projectId: null,
        adminId: null,
        auditStatus: null,
        auditBatchId: null
      },
      // 筛选下拉框
      orgList: [],
      officeList: [],
      capitalList: [],
      // 筛选内容
      searchData: {
        capitalId: null,
        chendanOrgId: null,
        manageOrgId: null,
        officeId: null,
        projectName: '',
        auditStatus: 1, // 查询通过的
        auditBatchType: 'AUDIT_IMPLEMENTATION_PLAN',
        ascending: false,
        orderBy: 'projectId',
        pageNo: 1,
        pageSize: 10
      },
      isScreen: false,
      curTime: '',
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '项目名称',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row)
                }
              }
            }, params.row.projectName);
          }
        },
        // {
        //   title: '总投资额',
        //   key: 'allFinancial',
        //   width: 100
        // },
        {
          title: '承担单位',
          key: 'chengdanOrg'
        },
        {
          title: '主管单位',
          key: 'manageOrg'
        },
        {
          title: '资金类别',
          key: 'capitalName'
        },
        {
          title: '主管部门',
          key: 'officeName'
        }
      ],
      investmentList: [
      ],
      total: 0
    }
  },
  mounted() {
    // 资金类别
    this.getFundData()
    // 部门
    this.getOfficeList()
    // 机构设置列表（主管和承担单位）
    this.getOrganizeData()
    // 表格数据
    this.getInvestmentData()
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.getInvestmentData()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.getInvestmentData()
    },
    // 资金类别下拉框
    getFundData() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/capital/searchCapital/list', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.capitalList = res.data.capitalList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    },
    // 打开详情弹出来
    showInfo(row) {
      this.popUpSearchData = {
        projectId: row.projectId,
        isOwnProject: true
      }
      this.$nextTick(() => {
        this.$refs.implementPlan.search();
      })
    },
    getInvestmentData() {
      const that = this
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/statistics/investment/list', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          that.investmentList = data.investmentList
          that.total = data.total
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
    },
    getOrganizeData() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/organization/searchOrganization/list', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.orgList = res.data.orgList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 部门下拉框
    getOfficeList() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/office/searchOffice/list', { orgId: null, officeName: '' }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.officeList = res.data.officeList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    }
  }
}
</script>



